import { Home } from "./modules/home.js";
import { headNav } from "./modules/headNav.js";
import { Sidebar } from "./modules/sidebar.js";
import { Footer } from "./modules/footer.js";
import { Contact } from "./modules/contact.js";
import { el, $, $$, setTitle } from "./modules/utils.js";
import videos from "./data/videos.json5";
import "./style/main.css";

(function () {
  const pages = {
    videos() {
      const items = (videos || [])
        .map((v) => {
          const url = v.url || `https://www.youtube.com/watch?v=${v.id}`;
          const title = v.title || "Untitled";
          const desc = v.description || "";
          return `
            <li class="video-item">
              <a class="video-link" href="${url}" target="_blank" rel="noopener">${title}</a>
              ${desc ? `<p class="video-desc">${desc}</p>` : ""}
            </li>
          `;
        })
        .join("");

      return `
        <h1>Videos</h1>
        <ul class="video-list">${items}</ul>
      `;
    },

    about() {
      return `
        <h1>About</h1>
        <p>Hi! I’m Amy. I like narrative games, gentle sims, weird web art, and snacks.</p>
        <p>This site is a work-in-progress.</p>
      `;
    },

    _notFound() {
      return `<h1>Not Found</h1><p>That page isn’t ready yet.</p>`;
    },
  };

  function render(key) {
    const main = $("main");
    main.innerHTML = "";

    if (key === "home") {
      Home({ videos });
    } else if (key === "contact") {
      Contact();
    } else {
      const view = pages[key] || pages._notFound;
      main.innerHTML = view();
    }

    $$(".nav-btn").forEach((btn) => {
      btn.classList.toggle("selected", btn.dataset.page === key);
    });

    setTitle(key);
  }

  document.addEventListener("DOMContentLoaded", () => {
    const body = document.body;


    const { header, list } = headNav(() => render("home"));
    body.appendChild(header);

    const mainContainer = el("div", { className: "main-container", parent: body });
    const section = el("div", { className: "section-container", parent: mainContainer });

    Sidebar({
      parent: section,
      latestUrl:
        (videos && videos[0] && (videos[0].url || `https://www.youtube.com/watch?v=${videos[0].id}`)) ||
        "#",
      playlists: [],
    });

    el("main", { className: "main-body", parent: section });
    el("aside", { className: "junior-body", parent: section });

    [
      ["home", "Home"],
      ["videos", "Videos"],
      ["about", "About"],
      ["contact", "Contact"],
    ].forEach(([key, label]) => {
      const li = el("li", { parent: list });
      const btn = el("button", {
        className: "nav-btn",
        text: label,
        attrs: { "data-page": key, type: "button" },
        parent: li,
      });
      btn.addEventListener("click", () => render(key));
    });

    Footer(mainContainer);

    render("home");
  });
})();
