___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --bg: #0c0b12;              /* starry night base */
  --text: #fffaf2;            /* soft off-white */
  --muted: #8a7f99;

  --brown: #5e4633;           /* header / blocks */
  --yellow: #ffd86b;          /* accents / highlights */
  --lavender: #c7b7ff;        /* accents */
  --lavender-deep: #a893e2;

  --card: #1a1522;            /* main surfaces */
  --line: #2a2235;
}

html, body {
  height: 100%;
}

body {
  margin: 0;
  color: var(--text);
  background-color: var(--bg);
  background-image:
    radial-gradient(1px 1px at 20% 30%, rgba(255,255,255,0.15) 0, transparent 40%),
    radial-gradient(1px 1px at 80% 70%, rgba(255,255,255,0.12) 0, transparent 40%),
    radial-gradient(1px 1px at 60% 20%, rgba(255,255,255,0.12) 0, transparent 40%),
    radial-gradient(1px 1px at 35% 80%, rgba(255,255,255,0.09) 0, transparent 40%);
  font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, "Helvetica Neue", Arial,
               "Noto Sans", "Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  line-height: 1.45;
}

a {
  color: var(--lavender);
  text-decoration: none;
}
a:hover { color: var(--lavender-deep); text-decoration: underline; }

h1, h2, h3 {
  line-height: 1.2;
  margin: 0 0 12px;
}
p { margin: 0 0 12px; }

.head-nav {
  background: var(--brown);
  border-bottom: 1px solid #2b231f;
}

.head-nav .home-link {
  display: inline-block;
  padding: 14px 18px;
  font-weight: 700;
  color: var(--yellow);
}

.head-nav .link-nav {
  display: block;
  border-top: 1px solid #2b231f;
}

.nav-list {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px 12px;
  margin: 0;
  list-style: none;
}

.nav-btn {
  background: #2f2530;
  color: var(--text);
  border: 1px solid #3b3243;
  border-radius: 8px;
  padding: 8px 12px;
  font: inherit;
  cursor: pointer;
}
.nav-btn:hover { border-color: var(--lavender); }
.nav-btn.selected {
  border-color: var(--yellow);
  box-shadow: 0 0 0 2px rgba(246,214,107,0.15) inset;
}

.main-container {
  min-height: calc(100vh - 64px);
  display: flex;
  flex-direction: column;
}

.section-container {
  display: flex;
  gap: 16px;
  flex: 1;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 16px;
}

.side-nav-container {
  flex: 0 0 260px;
}

.side-nav {
  background: var(--card);
  border: 1px solid var(--line);
  border-radius: 12px;
  padding: 16px;
}
.side-nav h2 {
  font-size: 1rem;
  font-weight: 800;
  letter-spacing: .02em;
  color: var(--yellow);
  margin-bottom: 10px;
}
.side-nav-list {
  list-style: none;
  margin: 10px 0 0;
  padding: 0;
}
.side-nav-list li + li { margin-top: 6px; }

main.main-body {
  flex: 1 1 auto;
  min-width: 0; 
  background: var(--card);
  border: 1px solid var(--line);
  border-radius: 12px;
  padding: 20px;
}

.junior-body {
  flex: 0 0 300px;
  min-width: 0;
}

.video-container {
  margin: 12px 0 16px;
  background: #0f0d18;
  border: 1px solid var(--line);
  border-radius: 12px;
  overflow: hidden;
}

.video-container .video {
  display: block;
  width: 100%;
  height: 315px;
  border: 0;
}

.home-cta {
  display: flex;
  gap: 12px;
}
.home-cta button {
  background: var(--lavender);
  color: #0b0b10;
  border: 1px solid #b7a0f0;
  border-radius: 999px;
  padding: 10px 14px;
  font-weight: 700;
  cursor: pointer;
}
.home-cta button:hover {
  background: var(--lavender-deep);
}

.video-list {
  list-style: none;
  margin: 12px 0 0;
  padding: 0;
}
.video-item {
  background: #151024;
  border: 1px solid var(--line);
  border-radius: 10px;
  padding: 12px;
}
.video-item + .video-item { margin-top: 10px; }
.video-link { font-weight: 700; }
.video-desc { color: var(--muted); margin-top: 6px; }

.contact-form {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
  margin-top: 10px;
}
.contact-form input,
.contact-form textarea {
  background: #120f1b;
  color: var(--text);
  border: 1px solid var(--line);
  border-radius: 8px;
  padding: 10px 12px;
  font: inherit;
}
.contact-form input::placeholder,
.contact-form textarea::placeholder { color: #8e87a6; }
.contact-form button[type="submit"] {
  background: var(--yellow);
  color: #1f1a13;
  border: 1px solid #e7c45d;
  border-radius: 10px;
  padding: 10px 14px;
  font-weight: 800;
  cursor: pointer;
}
.contact-form button[type="submit"]:hover {
  filter: brightness(1.03);
}

.site-footer,
footer {
  color: var(--muted);
  text-align: center;
  padding: 20px 16px 40px;
}

@media (max-width: 900px) {
  .section-container {
    flex-direction: column;
  }
  .side-nav-container,
  .junior-body {
    flex: 0 0 auto;
  }
  .video-container .video {
    height: 220px;
  }
}

html,body{height:100%;}
*{box-sizing:border-box;}
img{max-width:100%;display:block;}
ul,ol{margin:0;padding:0;list-style:none;}

body{
  margin:0;
  color: var(--paper);
  background:
    radial-gradient(1200px 800px at 20% -10%, #251436 0%, transparent 60%),
    radial-gradient(1000px 700px at 90% 10%, #2a1438 0%, transparent 60%),
    radial-gradient(1600px 900px at 50% 120%, #120a1a 0%, transparent 60%),
    var(--ink);
  font: 16px/1.5 system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
}

.main-container{
  min-height:100vh;
  display:flex;
  flex-direction:column;
}

.head-nav{
  position:sticky; top:0; z-index:10;
  background: rgba(17, 10, 25, .7);
  backdrop-filter: blur(6px);
  border-bottom: 1px solid rgba(199,183,255,.15);
}
.head-nav .home-link{
  display:inline-block;
  padding:14px 16px;
  text-decoration:none;
  color: var(--butter);
  font-weight:700;
}
.link-nav{
  display:flex;
  align-items:center;
}
.nav-list{
  display:flex;
  gap:8px;
  padding:8px 12px 12px;
}
.nav-btn{
  appearance:none;border:0;
  padding:8px 12px;border-radius:10px;
  background: rgba(199,183,255,.12);
  color: var(--paper); cursor:pointer;
}
.nav-btn.selected{ outline:2px solid var(--ring); }

.section-container{
  width:min(1100px, 100% - 32px);
  margin-inline:auto;
  padding:20px 0 28px;
  display:flex;
  gap:24px;
  flex:1; 
}

.main-body{
  flex: 1 1 680px; 
  min-width:0;     
  background: rgba(255,250,242,.03);
  border: 1px solid rgba(199,183,255,.12);
  border-radius:16px;
  padding:16px;
}

.junior-body{
  flex: 0 0 280px;  
  min-width: 260px;
  background: rgba(255,250,242,.03);
  border: 1px solid rgba(199,183,255,.12);
  border-radius:16px;
  padding:16px;
}

.card{
  background: rgba(255,250,242,.06);
  border: 1px solid rgba(199,183,255,.12);
  border-radius:14px;
  padding:12px;
}

.site-footer{
  color: var(--muted);
  padding:18px 16px 28px;
  text-align:center;
  border-top: 1px solid rgba(199,183,255,.12);
}

.home-hero h1{ margin: 0 0 4px; color: var(--lav); }
.home-cta{
  display:flex; gap:10px; flex-wrap:wrap; margin-top:12px;
}
.home-cta button{
  padding:8px 12px; border-radius:999px; border:1px solid rgba(199,183,255,.2);
  background: rgba(199,183,255,.08); color: var(--paper); cursor:pointer;
}

.list-container{ margin-top:10px; }
.list .item{
  padding:10px 0; border-bottom:1px dashed rgba(199,183,255,.15);
}
.nav-anchor{ color: var(--butter); text-decoration:none; }
.nav-anchor:hover{ text-decoration:underline; }
.video-desc{ color: var(--muted); margin:.25rem 0 0; }

.contact h1{ margin:0 0 6px; color: var(--lav); }
.contact-form{
  display:grid; gap:10px; margin-top:10px;
}
.contact-form input,
.contact-form textarea{
  width:100%;
  padding:10px 12px; border-radius:10px;
  background: rgba(255,250,242,.08);
  border:1px solid rgba(199,183,255,.18);
  color: var(--paper);
}
.contact-form button{
  justify-self:start;
  padding:8px 14px; border-radius:10px; border:0;
  background: linear-gradient(180deg, var(--butter), #ffb94d);
  color:#261a0e; font-weight:700; cursor:pointer;
}

@media (max-width: 900px){
  .section-container{ flex-direction:column; }
  .junior-body{ flex: 0 0 auto; min-width: unset; order:-1; }
}
